  display: flex;
  flex-direction: column;
  flex-grow: 1;
  height: 90%;
  margin: 15px; }

.pane-header {
  background: rgba(0, 0, 0, 0.3);
  border-bottom: 1px solid black;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 300;
  padding: 5px 8px;
  width: 100%;
  height: 30px; }
  .pane-header div {
    display: flex; }
    .pane-header div p {
      margin: 0;
      margin-left: 10px; }

.expand-button {
  cursor: pointer;
  -webkit-transition: color 250ms;
  transition: color 250ms; }
  .expand-button:hover {
    color: lightblue; }

.modal {
  width: 90% !important; }

